import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { UserContext } from '../provider/UserContext'
class CommentInput extends Component {
  static propTypes = {
    username: PropTypes.string,
    email: PropTypes.string,
    onSubmit: PropTypes.func,
  }
  // TODO: æƒ³æƒ³è¿™è¾¹çš„ç”¨æˆ·åæ€ä¹ˆè·å–
  static contextType = UserContext

  constructor(props) {
    super(props)
    this.state = {
      username: '',
      content: '',
    }
  }

  componentDidMount() {
    this.textarea.focus()
  }

  handleUsernameChange(event) {
    this.setState({
      username: event.target.value,
    })
  }

  handleContentChange(event) {
    this.setState({
      content: event.target.value,
    })
  }

  handleSubmit(e) {
    e.preventDefault()

    if (this.props.onSubmit) {
      const { username, content } = this.state
      const email = this.props.email
      this.props.onSubmit({
        username,
        email,
        content,
        createdTime: +new Date(),
      })
    }
    this.setState({ content: '' })
  }

  render() {
    return (
      <div className='comment-input'>
        <div className='comment-field'>
          <span className='comment-field-name'>ãƒ¦ãƒ¼ã‚¶åï¼š</span>
          <div className='comment-field-input'>
            <input
              value={this.state.username}
              onChange={this.handleUsernameChange.bind(this)}
              // è¿™é‡Œçš„refçš„ä½œç”¨æ˜¯ å°†è¿™ä¸ªinputå‘½åä¸ºinputName
              // å¹¶ä¸”è®¾ç½®ä¸ºç»„ä»¶å®ä¾‹çš„ä¸€ä¸ªå±æ€§
              // ä¹‹åå°±å¯ä»¥é€šè¿‡this.inputNameæ¥è·å–è¿™ä¸ªDOMå…ƒç´ 
              // æ¯”å¦‚å¯ä»¥åœ¨componentDidMounté‡Œé¢ä½¿ç”¨è¿™ä¸ªDOMå…ƒç´ 
              // å¹¶ä¸”è°ƒç”¨ç±»ä¼¼this.inputName.focus()ä¹‹ç±»çš„DOM API
              // å°±å¯ä»¥è¾¾åˆ°åŠ è½½å®Œé¡µé¢è‡ªåŠ¨focusåˆ°è¾“å…¥æ¡†çš„æ•ˆæœ
              // *** ä½†æ˜¯ï¼ï¼ï¼èƒ½ç”¨reactçš„æœºèƒ½åšåˆ°çš„äº‹ä¸€å®šä¸è¦ç”¨ref
              // *** å¹¶ä¸” æ€»ä¹‹ä¸åˆ°ä¸‡ä¸å¾—å·²å°½é‡ä¸è¦ç”¨ref
              // ref={(inputName) => (this.inputName = inputName)}
            />
          </div>
        </div>
        <div className='comment-field'>
          <span className='comment-field-name'>ã‚³ãƒ¡ãƒ³ãƒˆï¼š</span>
          <div className='comment-field-input'>
            <textarea
              ref={(textarea) => (this.textarea = textarea)}
              value={this.state.content}
              onChange={this.handleContentChange.bind(this)}
            />
          </div>
        </div>
        <div className='comment-field-button'>
          <button onClick={this.handleSubmit.bind(this)}>æŠ•ç¨¿</button>
        </div>
      </div>
    )
  }
}

export default CommentInput
